<template><!------------提现地址----------------->
    <div class="p-20">
        <div class="filter-container" style="margin-bottom: 20px">
            <el-input class="filter-item" style="width: 300px;" placeholder="请输入会员UID、手机号码、地址" @keyup.native.enter="handleFilter"
                      v-model="listQuery.keyword">
            </el-input>
            <span>虚拟币类型</span>
            <el-select clearable style="width: 150px" class="filter-item" v-model="listQuery.introduceId"
                       placeholder="全部类型">
                <el-option v-for="status in coinList" :key="status.baseSeq" :label="status.chineseName"
                           :value="status.baseSeq">
                </el-option>
            </el-select>
            <el-button class="filter-item" @click="handleFilter" size="small"  style="margin-left: 30px">查询</el-button>

        </div>
        <div>
            <el-table
                    ref="singleTable"
                    :data="tableData"
                    v-loading="listLoading"
                    element-loading-text="给我一点时间"
                    style="width: 100%; ">
                <el-table-column
                        v-for="item in tableHeader"
                        :key="item.prop"
                        :label="item.label"
                        :prop="item.prop"
                        :width="item.width"
                        :min-width="item.minWidth"
                        :align="item.align"
                        :formatter="item.formatter"
                        show-overflow-tooltip>
                </el-table-column>
            </el-table>
            <div v-show="!listLoading" class="pagination">
                <el-pagination @size-change="userPageSize" @current-change="userPage"
                               :current-page="listQuery.page"
                               :page-sizes="[15,30,45,60]" :page-size="listQuery.pageSize"
                               layout="total, sizes, prev, pager, next, jumper" :total="total">
                </el-pagination>
            </div>
        </div>
        <!------------------------------------------------下面为查看身份信息---------------------------------------------------------------------------->
    </div>
</template>

<script>
    /*import fetch from '@/utils/fetch'*/
    import DCTP from '@/utils/dctp';
    import {getwithdrawlist} from '@/api/useraddress';
    import {getgoodslist} from '@/api/awaits';

    const allCoin = {baseSeq: 0, chineseName: '全部类型', englishName: ""};
    export default {
        data() {
            return {
                allCoin,
                coinList: [],
                goodsall: {
                    types: [1]
                },
                //总条数
                total: null,
                //返回来列表的数据
                tableData: [],
                listLoading: true,
                listQuery: {
                    typeId: 1,
                    keyword: '',
                    introduceId: 0,
                    page: 1,
                    pageSize: 15
                },
                tableHeader: [
                    {
                        label: "UID",
                        prop: "uid",
                        width: 80
                    }, {
                        label: "登录名",
                        prop: "signtype",
                        width: 180,
                        align: 'center',
                        formatter(row, column, value) {
                            return value === 1 ? row.phoneNum : (value === 2 ? row.email : '未知');
                        }
                    }, {
                        label: "邮箱地址",
                        prop: "email",
                        width: 170
                    }, {
                        label: "真实姓名",
                        prop: "name",
                        width: 180
                    }, {
                        label: "资产名称",
                        prop: "coinName",
                        width: 180,
                    }, {
                        label: "充值地址",
                        prop: "address",
                        width: 400,

                    }, {
                        label: "备注",
                        prop: "checkRemark",
                        width: 300,

                    }, {
                        label: "创建时间",
                        prop: "createTime",
                        width: 180,
                        formatter(row, column, value) {
                            return DCTP.utils.parseDateTimeStr(value)
                        }

                    }
                ]
            }

        }
        ,
        computed: {
            tableDataLength() {
                return this.tableData.length;
            }
        }
        ,
        methods: {
            getList() {
                this.listLoading = true;
                getwithdrawlist(this.listQuery).then(response => {
                    if (!response.result) {
                        this.msg = response.msg;
                        this.$message({
                            type: 'warning',
                            message: this.msg
                        });
                        this.listLoading = false;
                    } else {
                        this.tableData = response.data.list;
                        this.coinList = response.data.coinlist;
                        this.coinList.unshift(this.allCoin);
                        this.total = response.data.total;
                        this.listLoading = false;
                    }
                })
            },
            /**
             * 条件筛选查询对象
             */
            handleFilter() {
                this.getList();
            },
            //获取当前页数
            userPage(val) {
                this.listQuery.page = val;
                this.getList();
            },
            //获取每页多少条
            userPageSize(val) {
                this.listQuery.pageSize = val;
                this.getList();
            },
            getgoods() {
                getgoodslist(this.goodsall).then(response => {
                    this.coinList = response.data;
                    this.coinList.unshift(this.allCoin);
                })
            }

        },
        created() {
            this.getList();
            this.getgoods();
        }
    }
</script>


